body {
  height: 90vw;
  background-color: #c3f3da;
  flex-direction: column;
  font-family: Arial, Helvetica, sans-serif;
  display: flex;
}

.search-block {
  float: left;
  width: 70vw;
  align-self: auto;
  display: flex;
}

.loader {
  color: gray;
  align-self: center;
  margin-left: .5vw;
  font-size: 16px;
}

#search-box {
  height: 4vh;
  width: 40vw;
  border: 2px solid #c3f3da;
  border-radius: 8px;
  padding-left: 10px;
  font-size: 2.5vh;
}

#search-result {
  flex-direction: column;
  list-style-type: none;
  display: flex;
}

.country-info {
  flex-direction: column;
  margin-top: 6px;
  display: flex;
}

#search-result > li {
  display: f;
  flex-direction: column;
}

.country-general {
  flex-direction: row;
  grid-template-columns: 5vw auto;
  justify-content: start;
  align-self: auto;
  display: grid;
}

.country-flag {
  columns: 1;
  max-height: 2.5vw;
  align-self: center;
  justify-self: center;
  display: grid;
}

.country-name {
  align-self: center;
  padding-left: .5vw;
  font-size: 3vw;
  font-weight: bold;
}

.country-details {
  grid-template-rows: repeat(3, max-content);
  grid-template-columns: max-content;
  justify-content: start;
  display: grid;
}

.country-details > div {
  grid-template-columns: 10vw auto;
  justify-content: start;
  display: grid;
}

.country-details-category {
  grid-column: 1;
  margin: .25vw;
  font-size: 1.5vw;
  font-weight: bold;
  display: grid;
}

.country-datails-value {
  grid-column: 2;
  align-content: center;
  margin: .25vw;
  font-size: 1.5vw;
  display: grid;
}

